import { Block, Controller, Commands } from '@ali/4ever-cangjie';

export default (controller: Controller, block: Block): void => {
  const { selection } = controller.value;
  if (selection) {
    controller
      .command(Commands.moveAnchorToStartOfNode, block)
      .command(Commands.moveFocusToStartOfNode, block);
  }
  controller.command(Commands.blur);
};
